import React from "react";
import "./login.css";
import { loginUrl } from "../../spotify";

function Login() {
  return (
    <div className="login">
      <img
        src="https://stuff.co.za/wp-content/uploads/2019/09/Spotify_logo_horizontal_black-scaled.jpg"
        alt=""
      />
      <a href={loginUrl}>LOGIN WITH SPOTIFY</a>
    </div>
  );
}

export default Login;
